<template>

  <div class="todoList">
    <header>
      <h1 class="todoList__title"><img src="./assets/logo.png" height="200" width="200" :title="title"/></h1>
      <add :addTodo="addTodo"></add>
    </header>

    <main>
      <sort :filterOptions="filterOptions"></sort>
      <list :todos="todos"
            :filterOptions="filterOptions"
      ></list>
    </main>

    <footer>
      create by&nbsp;<a href="https://richg0ld.github.io">richg0ld</a>.
    </footer>
  </div>
</template>

<script>
  import Add from './components/Add'
  import Sort from './components/Sort'
  import List from './components/List'

  export default {
    name: 'todoList',
    components: {
      Add,
      Sort,
      List
    },
    data () {
      return {
        title: 'TodoListTodoListTodoList',
        filterOptions: {
          important: ["모두"],
          comp: ["모두"]
        },
        todos: [
          {
            "comp":true,
            "important": "중요",
            "date": "2016.12.15",
            "text": "테스트용 데이터 입니다. 확인해주셔서 감사합니다."
          }, {
            "comp":false,
            "important": "매우중요",
            "date": "2016.12.15",
            "text": "Blah, Blah."
          }
        ]
      }
    },
    methods: {
      addTodo (todo) {
        this.todos.push(todo);
      }
    }
  }
</script>

<style scoped>
  header {
    position:fixed;
    left: 0;
    top: 0;
    width:100%;
    background-color:#fff;
    display:flex;
    border-bottom:1px solid #ededed;
  }
  .todoList__title {
    width:30px;
    height:50px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .todoList__title img{
    width:25px;
    height:25px;
  }
  main{
    padding:50px 0;
  }
  footer{
    position: absolute;
    bottom:0;
    left:0;
    width:100%;
    display: flex;
    height:50px;
    background-color:#727f80;
    color:#fff;
    align-items: center;
    justify-content: center;
  }
  footer a{
    color:#fff;
  }
</style>



