<template>
  <div class="list">
    <ul>
      <li v-for="todo in filteredTodos">
        <detail :todo="todo"></detail>
        <div class="list__drag--change-position"></div>
      </li>
    </ul>
  </div>
</template>

<script>
  import Detail from './Detail.vue'

  export default {
    name: 'list',
    props: ['todos', 'filterOptions'],
    components:{
      Detail
    },
    data () {
      return {}
    },
    computed: {
      filteredTodos() {
        return this.todos.filter(v=>{
          if(this.filterOptions.important.includes("모두")) return true;
          return this.filterOptions.important.includes(v.important);
        }).filter(v=>{
          if(this.filterOptions.comp.includes("모두")) return true;
          return this.filterOptions.comp.includes(v.comp ? "완료" : "미완료");
        });
      }
    }
  }
</script>

<style scoped>
  .list ul li{
    display: flex;
    border-bottom: 1px solid #ddd;
    min-height:72px;
  }
  .list__drag--change-position{
    width:30px;
    background-color:#f8f8f8;
    cursor: pointer;
  }
  .list__drag--change-position:hover{
    background-color:#ecf9f3;
  }
</style>



