<template>
  <div class="info">
    <dl class="info__todo-data--comp-date">
      <dt>중요도 :</dt><dd>{{ todo.important }}</dd><br/>
      <dt>추가일 :</dt><dd>{{ todo.date }}</dd>
    </dl>
    <p class="info__todo-data--todo" @dblclick="editTodo(todo)">{{ todo.text }}</p>
  </div>
</template>

<script>
  export default {
    name: 'info',
    props: ['todo', 'editTodo'],
    data () {
      return {}
    }
  }
</script>

<style scoped>
  .info{
    flex: 1;
  }
  .info__todo-data--comp-date{
    font-size:11px;
    color:#cccccc;
  }
  .info__todo-data--comp-date dt, .info__todo-data--comp-date dd{
    display:inline-block;
  }
  .info__todo-data--todo{
    color:#7f8c8d;
    font-weight: bold;
  }
</style>



