<template>
  <section class="sort">
    <div class="sort--important">
      <span v-for="(importantOption, i) in importantOptions" class="sort__checkbox--important">
          <input type="checkbox" name="important"
                 :id="'fi_'+i"
                 :title="importantOption"
                 :value="importantOption"
                 v-model="filterOptions.important"
                 @change="sortImportant"
          />
          <label :for="'fi_'+i">{{ importantOption }}</label>
      </span>
    </div>
    <div class="sort--complete">
      <span v-for="(completeOption, i) in completeOptions" class="sort__checkbox--complete">
          <input type="checkbox" name="comp"
                 :id="'fc_'+i"
                 :title="completeOption"
                 :value="completeOption"
                 v-model="filterOptions.comp"
                 @change="sortComplete"
          />
          <label :for="'fc_'+i">{{ completeOption }}</label>
      </span>
    </div>
  </section>
</template>

<script>

  export default {
    name: 'sort',
    props: ['filterOptions'],
    data () {
      return {
        importantOptions: ["모두", "매우중요", "중요", "보통"],
        completeOptions: ["모두", "완료", "미완료"]
      }
    },
    methods: {
      sortImportant(e) {
        this.filterOptions.important = this.filterOptions.important.filter(e.target.value === "모두" ?  v => v === "모두" : v => v !== "모두");
      },
      sortComplete(e) {
        this.filterOptions.comp = this.filterOptions.comp.filter(e.target.value === "모두" ?  v => v === "모두" : v => v !== "모두");
      }
    }
  }
</script>

<style scoped>
  .sort{
    padding:5px;
    background-color:#f6f6f6;
    border-bottom:1px solid #ededed;
  }
  .sort--complete{
    margin-top:5px;
  }
  .sort__checkbox--important, .sort__checkbox--complete{
    display: inline-block;
    margin-left:5px;
  }
  .sort__checkbox--important:first-child, .sort__checkbox--complete:first-child{
    margin-left:0;
  }
  .sort__checkbox--important input, .sort__checkbox--complete input{
    position: absolute;
    left: 0;
    top: 0;
    width: 0;
    height: 0;
    opacity: 0;
  }
  .sort__checkbox--important label, .sort__checkbox--complete label{
    display: inline-block;
    padding:3px;
    border: 1px solid #4fc08d;
    color:#4fc08d;
    border-radius: 15px;
  }
  .sort__checkbox--important input:checked + label, .sort__checkbox--complete input:checked + label{
    color:#ffffff;
    background-color:#4fc08d;
  }
</style>



