<template>
  <div class="detail">
    <input class="detail__checkbox--comp" type="checkbox" name="comp" v-model="todo.comp" />
    <info v-if="!isEditing"
          :todo="todo"
          :editTodo="editTodo"
    ></info>
    <modify v-else
            :todo="todo"
            :doneEditTodo="doneEditTodo"
            :cancelEditTodo="cancelEditTodo"
    ></modify>
  </div>
</template>

<script>
  import Info from './Info.vue'
  import Modify from './Modify.vue'

  export default {
    name: 'detail',
    props: ['todo'],
    components:{
      Info,
      Modify
    },
    data () {
      return {
        isEditing:false,
        beforeEditCache: {
          important:"",
          text:""
        }
      }
    },
    methods:{
      editTodo () {
        this.isEditing = true;
        this.beforeEditCache.important=this.todo.important;
        this.beforeEditCache.text=this.todo.text;
      },
      doneEditTodo () {
        const d = new Date();
        this.todo.date = `${d.getFullYear()}.${d.getMonth()+1}.${d.getDate()}`;
        this.isEditing = false;
        this.beforeEditCache.important="";
        this.beforeEditCache.text="";
      },
      cancelEditTodo () {
        this.isEditing = false;
        this.todo.important = this.beforeEditCache.important;
        this.todo.text = this.beforeEditCache.text;
        this.beforeEditCache.important="";
        this.beforeEditCache.text="";
      }
    }
  }
</script>

<style scoped>
  .detail{
    flex: 1;
    display:flex;
    align-items:center;
    padding:10px 0;
  }
  .detail__checkbox--comp{
    margin:0 13px;
    width:13px;
    height:13px;
  }

</style>



